import React, { useState, useEffect } from 'react';

import clsx from 'clsx';

import Navbar from 'react-bootstrap/Navbar';
import Image from 'react-bootstrap/Image';

import BeginPage from './BeginPage';
import FinishedPage from './FinishedPage';
import UnsuccessfulPage from './UnsuccessfulPage';
import logo from '../images/logo.png';

import './App.css';

export default function App() {
  const [DisplayPage, setDisplayPage] = useState(() => BeginPage);
  const [fade, setFade] = useState(true);

  useEffect(() => {
    setTimeout(() => setFade(true), 100);
  }, [DisplayPage]);


  return (
    <div>
      <Navbar bg="dark">
        <Navbar.Brand><Image src={logo} width={200} /></Navbar.Brand>
      </Navbar>
      <div className={clsx('fadeee', fade && 'app-fade-slide-in-animation')}>
        <DisplayPage
          onFinished={() => {setFade(false); setDisplayPage(() => FinishedPage)}}
          onFailed={() => {setFade(false); setDisplayPage(() => UnsuccessfulPage)}}
          onRetry={() => {setFade(false); setDisplayPage(() => BeginPage)}}
        /> 
      </div>
    </div>
  );
}


