import React from 'react';

import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

export default function UnsuccessfulPage({ onRetry }) {

  const handleRetry = (e) => {
    e.preventDefault();
    e.stopPropagation();
    onRetry();
  }
  return (
    <Container>
      <Row className="justify-content-center m-3">
        <Col xs="auto">
          <h1 className="display-4 text-center text-danger">Verification failed</h1>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <Col xs="auto">
          <a href="/" onClick={handleRetry}><h4 className="text-info text-center">Retry</h4></a>
        </Col>
      </Row>
    </Container>
  );
}


